<template>
  <div class="forgotpw">
    <div class="login-box">
      <div class="topbox">
        <van-nav-bar>
          <template #left>
            <div class="leftbox">
              <div class="logo">
                <img src="../assets/img/logo2.png" alt />
              </div>
              <div class="textbox">忘记密码</div>
            </div>
          </template>
          <template #right>
            <div class="dian" @click="gologin">想起来了</div>
          </template>
        </van-nav-bar>
      </div>
      <div class="bodybox">
        <div class="bodytext-box">
          <h2>请填写邮箱</h2>
        </div>
        <div class="bodyinput-box">
          <van-form>
            <van-field v-model="emaildata.email" name="填写绑定邮箱" placeholder="填写绑定邮箱" />
            <van-field class="yzm" v-model="emaildata.emailVs" name="验证码" placeholder="验证码"></van-field>
            <div class="getnum fr" @click="getemailvs">{{emaildata.getemailvsTxt}}</div>
            <div class="submit-btn">
              <van-button round color="#3a63f1" native-type="submit" is-link @click="checkvs">下一步</van-button>
            </div>
            <van-popup
              round
              closeable
              close-icon-position="top-right"
              position="bottom"
              :style="{ height: '60%' }"
              v-model="isShowPw"
            >
              <div class="popupbox">
                <p>找回密码</p>
                <van-field
                  v-model="userInfo.phone"
                  name="输入绑定的手机号 中国(+86)"
                  placeholder="输入绑定的手机号 中国(+86)"
                />
                <van-field
                  v-model="userInfo.password"
                  :type="ispopupShowPw?'text':'password'"
                  name="输入新密码"
                  placeholder="输入新密码,字母开头的6-16为字符串"
                  :right-icon="ispopupShowPw?'eye-o':'closed-eye'"
                  @click-right-icon="popupshoweye"
                />

                <div class="pop-btn">
                  <van-button
                    round
                    color="#3a63f1"
                    native-type="submit"
                    is-link
                    block
                    @click="getfindpw"
                  >提交</van-button>
                </div>
              </div>
            </van-popup>
          </van-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/forgotpw.less";
import { utils } from "../assets/js/utils";
import { validForm } from "../assets/js/validForm";
export default {
  data() {
    return {
      isShowPw: false,
      ispopupShowPw: false,
      emaildata: {
        email: "",
        emailVs: "",
        getemailvsTxt: "获取验证码",
      },
      //账号密码
      userInfo: {
        phone: "",
        password: "",
      },
    };
  },
  methods: {
    //跳转到home
    gologin() {
      this.$router.push("/login");
    },
    showeye() {
      this.isShowPw = !this.isShowPw;
    },
    //获取验证码
    getemailvs() {
      let o = {
        email: {
          value: this.emaildata.email,
          reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
          errMsg: "邮箱格式不正确",
        },
      };
      if (!validForm.valid(o)) {
        return;
      }
      let num = 5;
      let setTime = setInterval(() => {
        this.emaildata.getemailvsTxt = --num + "秒";
        if (num == 0) {
          clearInterval(setTime);
          this.emaildata.getemailvsTxt = "重新获取";
        }
      }, 1000);

      let data = utils.queryString({
        appkey: this.appkey,
        email: this.emaildata.email,
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "POST",
        url: "/emailValidCode",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == "J001") {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //验证验证码
    checkvs() {
      let o = {
        validCode: {
          value: this.emaildata.emailVs,
          reg: /^\d{6}$/,
          errMsg: "验证码为6位数字",
        },
      };
      if (!validForm.valid(o)) {
        return;
      }

      let data = utils.queryString({
        appkey: this.appkey,
        validCode: this.emaildata.emailVs,
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "POST",
        url: "/checkValidCode",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == "K001") {
            this.$toast(result.data.msg);
            this.isShowPw = !this.isShowPw;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //显示密码
    popupshoweye() {
      this.ispopupShowPw = !this.ispopupShowPw;
    },
    //提交找回密码
    getfindpw() {
      let o = {
        phone: {
          value: this.userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errMsg: "手机号码不正确",
        },
        password: {
          value: this.userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errMsg: "密码请输入字母开头的6-15位字符串",
        },
      };
      if (!validForm.valid(o)) {
        return;
      }

      let data = utils.queryString({
        appkey: this.appkey,
        phone: this.userInfo.phone,
        password: this.userInfo.password,
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "POST",
        url: "/retrievePassword",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == "L001") {
            this.$toast(result.data.msg);
            this.$router.push("/login");
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>